import {DataSource} from '../../utils/dataSource';
var queryURL = '/rest/configServer/access/table';
var ds = new DataSource({
  queryUrl: queryURL,
  createUrl: '/rest/configServer/access/table',
  updateUrl: '/rest/configServer/access/table',
  name: 'tableEntity'
});


var DS = {
  save(value) {
    if (value.id) {
      return ds.update(value, {id: value.id}).then(function (data) {
        return data.json()
      });
    } else {
      return ds.create(value).then(function (data) {
        return data.json()
      });
    }
  },
  findById(id) {
    return ds.query(queryURL, {id: id}).then(function (data) {
      return data.json();
    }).then(function (data) {
      return data.result[0]
    })
  },

  query(appId) {
    return ds.paged({appId: appId}).then(function (data) {
      return data.json();
    }).then(function (data) {
      return data.result
    })
  }

}


export {DS}



